.modalBackground {
  /* 모달창 크기 */
  width: 100%;
  height: 100%;
  margin: 0;
  /* background-color: pink; */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.smallMenu {
  position: absolute;
  z-index: 3;
  top: 235px;
  left: 380px;
  border: 1px solid #7d7d7d40;
  border-radius: 4px;
  background-color: white;
  display: none;
}
.smallMenu div:hover {
  background-color: #dcedd4;
}
.smallMenu button {
  border: none;
  background-color: transparent;
  font-size: 16px;
  color: #095544;
  cursor: pointer;
  padding: 5px;
  width: 100%;
  text-align: start;
}
@media (max-width: 935px) {
  .smallMenu {
    display: block;
  }
}
